.profile {
  max-width: 250px;
  width: 100%;

  display: flex;
  flex-direction: column;
  align-items: center;

  background-color: #fff;
  border-radius: 10px;

  box-shadow: 0 0 10px 0 #ccc;
}
.avatar {
  max-width: 100px;
  max-height: 100px;
  border-radius: 50%;
  margin-bottom: 30px;
}
.description {
  padding: 30px 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
}
.name {
  font-size: 20px;
  font-weight: 600;
}

.tag,
.location {
  color: #8d8d8d;
}

.stats {
  display: flex;
  width: 100%;
  background-color: #e3e3e3;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  overflow: hidden;
}

.stats li {
  padding: 20px 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-basis: 33.333%;
}
.stats li {
  border: 1px solid #dadada;
}
